*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(24, 201, 213);
  height: 100vh;
  overflow: hidden;
}
.tasks{
  border-radius: 10px;
  box-shadow: 8px 8px 3px rgb(43, 42, 42);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  width: 700px;
  min-height: 500px;
  background-color: gray;
}
h1{
  text-align: center;
  color: white;
}
.task{
  display: flex;
  justify-content: space-between;
  margin: 15px;
  border: rgb(40, 30, 177) 2px solid;
  border-radius: 5px;
  padding: 5px;
  text-align: center;
}
span{
  color: #fff;
  padding: 5px 0 5px 10px;
  font-size: 20px;
  font-weight: 500;
}
button{
  padding: 5px;
  margin: 5px;
  font-size: 14px;
  color: #fff;
  border: none;
  border-radius: 5px;
}
.add input{
  width: 90%;
  margin-left: 15px;
  height: 50px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid black;
  border-radius: 5px;
}